.LatestNotices {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.LatestNotices h4 {
    color: var(--primary-color);
    padding-bottom: 15px;
}

.latestNoticesList {
    display: flex;
    justify-content: left;

}

.latestNoticesList img {
    height: 30px;
    width: 30px;
}

.latestNoticesListTitle {
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    width: calc(100% - 40px);
}

.latestNoticesListTitle p {
    font-size: 10px;
    padding-bottom: 5px;
}

.ImportantLink {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.ImportantLink h4 {
    color: var(--primary-color);
    padding-bottom: 5px;
}

.ImportantLink p {
    font-size: 10px;
    padding: 2px 0px;
}